  <section class="interests">
    <h1 class="base__title">About Me</h1>
    <div class="interests__description">
      <p> Oh, hello there ! I'm Mos Daniele and I am currently a second year student at Babe-Bolyai University faculty of Mathematics and Informatics, pursuing a bachelor degree in Computer science.</p>
      <li> Most of my work can be found on my personal Github, also, more relevant projects can be found in the "Projects" section of this website.</li>
      <li> My coding career started with game development, and even to this day I still enjoy participating in some Game Jams from time to time with my friends.</li>
      <li> I made a couple of websites, some of which were e-commerce, and others were presentation websites. As freelancing projects I made desktop applications using Qt and C++ for file and work management.</li>
